.header {
  width: auto;
  height: 100%;
  display: flex;
  position: fixed;
  left: -150%;
  transition: 300ms;
  z-index: 1;
  margin-left: 64px;
}

.header__active {
  left: 0;
}

.header__container-button-background {
  position: fixed;
  width: 64px;
  height: 100%;
  background-color: #121212;
}

.button-burger {
  position: fixed;
  height: 36px;
  width: 36px;
  background-color: #121212;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 0;
  z-index: 10;
  padding: 0;
  outline: none;
  top: 33px;
  left: 14px;
}

.button-burger__svg-line {
  width: 100%;
  padding-bottom: 4px;
  stroke: #EEEEEE;
}

.button-burger:hover .button-burger__svg-line {
  stroke: #0EC261;
}

.button-burger__cross {
  position: fixed;
  stroke: #EEEEEE;
}

.button-burger__cross:hover {
  stroke: #0EC261;
}

.language {
  background-color: #121212;
  z-index: 10;
  color: #EEEEEE;
  font-size: 18px;
  font-family: "Roboto", Medium;
  border: 0;
  outline: none;
  position: fixed;
  bottom: 33px;
  left: 11px;
}

.language:hover {
  color: #0EC261;
}

.header__left-block {
  width: calc((100vw - 64px) / 2);
  min-width: 614px;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 64px;
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  background-color: #121212;
}

.header__content {
  position: fixed;
  width: auto;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 12;
}

.header__transparent-block {
  width: calc((100vw - 64px) / 2);
  height: 100vh;
  background-color: #121212;
  opacity: 0.9;
  z-index: 2;
  position: relative;
}

.header__link {
  position: relative;
  width: auto;
  text-decoration: none;
  color: #EEEEEE;
  font-size: 32px;
  font-family: "Roboto", Medium;
  margin-bottom: 15px;
  z-index: 6;
}

.header__link:hover {
  color: #0EC261;
}

.icons-social-media {
  padding-top: 10px;
}

.icons-social-media__icon:hover {
  fill: #0EC261;
}

.icons-social-media__icon {
  height: 32px;
  width: 32px;
  margin-right: 32px;
  fill: #EEEEEE;
}

.header__left-block_order {
  width: 100vw;
}

.header__transparent-block_order {
  display: none;
}

@media screen and (max-width: 1024px) {
  .header__left-block {
    min-width: 0;
  }

  .header__transparent-block {
    display: none;
  }

  .header__left-block {
    width: 100vw;
  }

  .header__link {
    font-size: 28px;
  }
}
@media screen and (max-width: 767px) {
  .header {
    margin-left: 0;
  }

  .language_active {
    display: none;
  }

  .header__link {
    font-size: 22px;
  }

  .header__left-block {
    padding-left: 28px;
  }

  .button-burger {
    background-color: #00000000;
    position: absolute;
  }

  .button-burger__svg-line {
    stroke: #121212;
  }

  .header__container-button-background {
    display: none;
  }
}
.slider {
  height: 100%;
  width: 50%;
  overflow: hidden;
  z-index: 0;
  box-sizing: border-box;
  position: relative;
}

.slider__tape {
  width: 400%;
  height: 100%;
  display: inline-flex;
  margin: 0;
  box-sizing: border-box;
  position: relative;
  transition: 300ms;
}

.slider__tape_1 {
  right: 0;
}

.slider__tape_2 {
  right: 100%;
}

.slider__tape_3 {
  right: 200%;
}

.slider__tape_4 {
  right: 300%;
}

.slider__button-arrow {
  position: fixed;
  z-index: 1;
  background-color: #00000000;
  border: 0;
  height: 100%;
  width: 64px;
}

.slider__button-arrow_green:hover {
  background-color: #0ec26238;
}

.slider__button-arrow_blue:hover {
  background-color: #007BFF38;
}

.slider__button-arrow_red:hover {
  background-color: #ff000038;
}

.slider__button-arrow_purple:hover {
  background-color: #80008038;
}

.slider__button-rigth {
  right: 0;
}

.slider__arrow {
  width: 10px;
  height: 20px;
}

.slider__wrapped-radio {
  position: absolute;
  display: inline-flex;
  z-index: 1;
  bottom: 0;
  width: 100%;
  justify-content: center;
  padding-bottom: 36px;
}

.slider__radio-button {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;
  z-index: -3;
}

.slider__radio-label {
  width: 8px;
  height: 8px;
  background-color: #EEEEEE;
  border-radius: 50%;
  margin-right: 10px;
}

.slider__radio-button:checked + .slider__radio-label {
  background-color: #0EC261;
}

