html {
    overflow-x: hidden;
    margin-right: calc(-1 * (100vw - 100%));
}

body { 
    margin: 0;
    padding: 0;
}

.app {
    width: 100vw;
    height: 100vh;
    margin: 0; 
    padding: 0;
    display: flex;
    z-index: -10;
}

.content {
    width: calc(100% - 64px) ;
    height: 100vh;
    margin-left: $widthHeader;
    background-color: $mainBlack;
    display: flex;
    z-index: 0;
}

@media screen and (max-width: $widthMobile) {
    .content {
        margin-left: 0;
        width: 100%;
    }
}

